import React, { useEffect, useState } from "react";
import axios from "axios";
import "./AdminPage.scss";
import "./AdminPage.css";

import Header from '../Header/Header';
import Footer from "../Footer/Footer";

import Button from "react-bootstrap/Button";
import Table from "react-bootstrap/Table";
import Form from "react-bootstrap/Form";
import Offcanvas from 'react-bootstrap/Offcanvas'

import { useAlert } from 'react-alert'

import { useSession } from 'react-session-persist';
import AdminSection from "../AdminSection/AdminSection";



function AdminPage() {
  
  const { authenticated, removeSession } = useSession();

  const [player1, setPlayer1] = useState({
    _id: '',
    team: '',
    teamScore:'', 
    position: '',
    _v: ''
  });

  const [player2, setPlayer2] = useState({
    _id: '',
    team: '',
    teamScore:'', 
    position: '',
    _v: ''
  });

  const [player3, setPlayer3] = useState({
    _id: '',
    team: '',
    teamScore:'', 
    position: '',
    _v: ''
  });

  const [player4, setPlayer4] = useState({
    _id: '',
    team: '',
    teamScore:'', 
    position: '',
    _v: ''
  });

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const alert = useAlert();


  
  function getScores() {
    axios.get("/scores").then((res) => {
      setPlayer1(res.data[0]);
      setPlayer2(res.data[1]);
      setPlayer3(res.data[2]);
      setPlayer4(res.data[3]);
    })
  }

  useEffect(function () {
    getScores();
  },[]);


  useEffect(function(){
    let lvl1 = player1.teamScore;
    let lvl2 = player2.teamScore;
    let lvl3 = player3.teamScore;
    let lvl4 = player4.teamScore;

    let allScores = [lvl1,lvl2,lvl3,lvl4];

    const sortedScores = allScores.slice().sort((a, b) => b - a);

    setPlayer1(prevState => ({
      ...prevState,
      position: sortedScores.indexOf(lvl1) + 1
    }));

    setPlayer2(prevState => ({
      ...prevState,
      position: sortedScores.indexOf(lvl2) + 1
    }));

    setPlayer3(prevState => ({
      ...prevState,
      position: sortedScores.indexOf(lvl3) + 1
    }));

    setPlayer4(prevState => ({
      ...prevState,
      position: sortedScores.indexOf(lvl4) + 1
    }));

  },[player1.teamScore, player2.teamScore, player3.teamScore, player4.teamScore]);
  

  function handlePlayer1ScoreChange(event){
    
    setPlayer1(prevState => ({
      ...prevState,
      teamScore: event.target.value
    }));

    console.log(player1);
  }

  function handlePlayer2ScoreChange(event){
    setPlayer2(prevState => ({
      ...prevState,
      teamScore: event.target.value
    }));

  }

  function handlePlayer3ScoreChange(event){
    setPlayer3(prevState => ({
      ...prevState,
      teamScore: event.target.value
    }));

  }

  function handlePlayer4ScoreChange(event){
    setPlayer4(prevState => ({
      ...prevState,
      teamScore: event.target.value
    }));

  }


  function handleUpdate(event){
    event.preventDefault();

    let scoreData = [player1,player2,player3,player4];

    axios.post("/update", {
       scoreData: scoreData
      }).then((res)=>{        
        alert.show(res.data);
      });
  }


  function handleReset(event){
    event.preventDefault();
    setPlayer1(prevState => ({
      ...prevState,
      teamScore: 0
    }));

    setPlayer2(prevState => ({
      ...prevState,
      teamScore: 0
    }));

    setPlayer3(prevState => ({
      ...prevState,
      teamScore: 0
    }));

    setPlayer4(prevState => ({
      ...prevState,
      teamScore: 0
    }));
  }


  async function handleLogout (event){
    event.preventDefault();

    removeSession({
      authenticated,
    });
    
  }



  return (  <> {authenticated ?  
    <div className="background-div">
      <Header/> 
      <div className="btn-div">
        <Button className="btnLogout" onClick={handleLogout}>Logout</Button>
      </div>
      <div className="tablediv">
        <Form>
          <Table striped bordered hover variant="dark">
            <thead>
              <tr>
                <th>Team</th>
                <th>Score</th>
                <th>Place</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{player1.team}</td>
                <td> <Form.Control type="number" autoComplete="off" onChange={handlePlayer1ScoreChange} value={player1.teamScore}  name="lvl1Score" placeholder="Enter Score" />  </td>
                <td>{player1.position}</td>
              </tr>
              <tr>
                <td>{player2.team}</td>
                <td> <Form.Control type="number" autoComplete="off" onChange={handlePlayer2ScoreChange} value={player2.teamScore}  name="lvl2score" placeholder="Enter Score" />  </td>
                <td>{player2.position}</td>
              </tr>
              <tr>
                <td>{player3.team}</td>
                <td> <Form.Control type="number" autoComplete="off" onChange={handlePlayer3ScoreChange} value={player3.teamScore}  name="lvl3score" placeholder="Enter Score" />  </td>
                <td>{player3.position}</td>
              </tr>
              <tr>
                <td>{player4.team}</td>
                <td> <Form.Control type="number"  autoComplete="off" onChange={handlePlayer4ScoreChange} value={player4.teamScore}  name="lvl4score" placeholder="Enter Score" />  </td>
                <td>{player4.position}</td>
              </tr>
            </tbody>
          </Table>
        
          <Button className="btnupdate" onClick={handleUpdate} >Update Scores</Button>
          <Button className="btnreset" onClick={handleReset}>Reset Scores</Button>
          <Button className="btn-instructions" onClick={handleShow}>Instructions</Button>

        </Form>        

      <Offcanvas show={show} onHide={handleClose} placement="bottom" className="offcanvas" bsPrefix="h-auto">
        <Offcanvas.Header closeButton className="canvas-head">
          <Offcanvas.Title >Instructions</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body className="canvas-div">
          <p>Change scores by clicking on the score you wish to change</p>
          <p>Click on the green "Update Score" button to save changes and make them visible to the audience</p>
          <p>Click the yellow "Reset Score" button to reset all scores to 0. **note this will not update the scores. This is expected behaviour to prevent accidental resets. In order to permanantly reset scores, click the reset button and then the update button</p>
          <p>If you accidentally clicked reset, refreshing the page will restore the previous scores</p>
        </Offcanvas.Body>
      </Offcanvas>

      </div>

      <Footer/>
      
    </div> : AdminSection} </>
  );
}

export default AdminPage;



