import React, { forwardRef } from "react";
import "./ClientPage.scss";
import "./ClientPage.css";

function ScoreItem(props) {
  return (
    <div className={"score-board" + props.position}>
      <div className="score-item">
        <h1>{props.team}</h1>
        <h2>{props.score}</h2>
      </div>
    </div>
  );
}

const ScoreItem1 = forwardRef((props, ref) => (
  <div ref={ref} className={"score-board" + props.position}>
    <div className="score-item">
      <h1>{props.team}</h1>
      <h2>{props.score}</h2>
    </div>
  </div>
));

export default ScoreItem;
