import React, { useState, useEffect } from "react";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import AdminPage from "../AdminPage/AdminPage";

import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

import "./AdminSection.css";
import "./AdminSection.scss";

import { useSession } from "react-session-persist";
import { useAlert } from "react-alert";
import axios from "axios";

function AdminSection() {
  const [username, SetUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showConfirm, setShowConfirm] = useState("hidden");

  const { authenticated, saveSession } = useSession();

  const alert = useAlert();

  function getLogin() {
    axios.get("/login").then((res) => {
      setShowConfirm(res.data);
    });
  }

  useEffect(function () {
    getLogin();
  }, []);

  function handleLogin(event) {
    event.preventDefault();

    if (showConfirm === "password") {
      newUserLogin();
    }
    else{
      UserLogin();
    }
  }

  function UserLogin(){
    if(checkfields()){
      axios.post('/login', {
        username: username,
        password: password
      }).then((res) => {
        if (res.data === "ok") {
          saveSession({ user: username });
          setShowConfirm("hidden");
          SetUsername("");
          setPassword("");
        }
        else{
          alert.show("error, incorrect credentials");
        }
      });
    }
  }


  function newUserLogin(){
    if (newPass()) {
      axios.post("/newlogin", {
        username: username,
        password: password
      }).then((res) => {
        if (res.data === "ok") {
          saveSession({ user: username });
          setShowConfirm("hidden");
          SetUsername("");
          setPassword("");
        }
        else{
          alert.show("error, try again");
        }
      });
    }
  }

  function newPass() {
    if (password === confirmPassword && checkfields()) {
      return true;
    } else {
      alert.show("passwords do not match");
      return false;
    }
  }

  function checkfields(){
    if(password === '' && username ===''){
      alert.show("fields cannot be blank");
      return false;
    }
    else{
      return true;
    }
  }

  function handleUserChange(event) {
    SetUsername(event.target.value);
  }

  function handlePassChange(event) {
    setPassword(event.target.value);
  }

  function handleConfirmPassChange(event) {
    setConfirmPassword(event.target.value);
  }

  return (
    <>
      {authenticated ? (
        <AdminPage />
      ) : (
        <>
          <Header />
          <div className="form-container">
            <div className="form-div">
              <Form>
                <Form.Control
                  type="text"
                  placeholder="username"
                  onChange={handleUserChange}
                  value={username}
                  autoComplete="off"
                  required={true}
                />
                <Form.Control
                  type="password"
                  placeholder="password"
                  onChange={handlePassChange}
                  value={password}
                  autoComplete="off"
                  required={true}
                />
                <Form.Control
                  type={showConfirm}
                  placeholder="confirm password"
                  onChange={handleConfirmPassChange}
                  value={confirmPassword}
                  autoComplete="off"
                />
                <Button className="btnLogin" onClick={handleLogin}>
                  Login
                </Button>
              </Form>
            </div>
          </div>
          <Footer />
        </>
      )}
    </>
  );
}

export default AdminSection;
